import React, { PureComponent } from "react";

const NetworkLike = (networkID, postID, Link) => {
  if (networkID == 1) return `https://twitter.com/intent/favorite?tweet_id=${atob(postID)}`
  else return Link
}
const NetworkComment = (networkID, postID, Link) => {
  if (networkID == 1) return `https://twitter.com/intent/tweet?in_reply_to=${atob(postID)}`
  else return Link
}


const SocialItm = ({ arialbl, hrefClick, Icon, iconColorStyle, count }) => {
  return <div className="tb_social_action__list">
    <div onClick={event => {
      event.stopPropagation()
      window.open(hrefClick, "_blank")
    }} className="tb_social_action__ico_wrap">
      <div className={`tb_social_action_ico__ tb__icon tb-${Icon}`} style={iconColorStyle}> </div>
      {count > 0 ? <div className="tb_social_action_counts__" style={iconColorStyle}>{count}</div> : ''}
    </div>
  </div>
}
export default class SocialAction extends PureComponent {
  render() {
    const { itemData, ThemeRule, actionClass, ThemeID } = this.props;

    const iconColorStyle = { color: ThemeID === 3 ? ThemeRule.iconType === 1 ? ThemeRule.iconColor : itemData.network.color : ThemeRule.fontColor }

    const networkID = itemData.network.id
    const likeIcon = (networkID === 3 || networkID === 10 || networkID === 7) ? 'like-alt' : 'heart-alt';
    const likeIconStatus = [1, 2, 3, 7, 8, 9, 10, 18].includes(networkID) ? true : false
    const commentIconStatus = [29, 19].includes(networkID) ? false : true
    const networkName = itemData.network.name ? itemData.network.name : "";
    return <>
      {(networkID !== 29 && networkID !== 26 && networkID !== 23 && networkID !== 6 && networkID !== 5 && networkID !== 11 && networkID !== 12 && networkID !== 15 && networkID !== 20 && networkID !== 21) ?
        <div className={actionClass}>
          <div className="tb_social_action__">
            {likeIconStatus ? <SocialItm arialbl={networkName} hrefClick={NetworkLike(networkID, itemData.postId, itemData.link)} Icon={likeIcon} iconColorStyle={iconColorStyle} count={itemData.like_count} /> : null}
            {commentIconStatus ? <SocialItm arialbl={networkName} hrefClick={NetworkComment(networkID, itemData.postId, itemData.link)} Icon={"comment"} iconColorStyle={iconColorStyle} count={itemData.comment_count} /> : null}
            {networkID === 1 ? <SocialItm arialbl={networkName} hrefClick={`https://twitter.com/intent/retweet?tweet_id=${atob(itemData.postId)}`} Icon={"retweet"} iconColorStyle={iconColorStyle} count={itemData.comment_count} /> : null}
            <SocialItm arialbl={networkName} hrefClick={itemData.link} Icon={"eye-alt"} iconColorStyle={iconColorStyle} count={0} />
          </div>
        </div> : ''}
    </>
  }
}