import React, { PureComponent, Suspense } from "react";
import ReactHtmlParser from 'react-html-parser';
import { STRING_TO_URL_CONVERT } from '../../../../utils'
import PostContentConvertion from "./PostContentConvertion";
export default class PostContent extends PureComponent {
  render() {
    const { contentClass, content, ThemeRule, font, personalization, item, contentTitle } = this.props;
    let contentData = item.network.id == 20 ? content : STRING_TO_URL_CONVERT(content);
    if (item.network.id == 12) contentData = ReactHtmlParser(contentData);  
    return (
      <div className={`${contentClass} tb-cTBfont- ${ThemeRule.font_varient}`} style={{ fontFamily: ThemeRule.css_font, fontSize: font.fontsize, color: font.fontColor }}>
        {contentTitle ? <div className="tb_bold_txt__">{ReactHtmlParser(contentTitle)}</div> : null}
        <Suspense fallback={<></>}> <PostContentConvertion data={item} key={contentData} content={contentData} Personalization={personalization} /></Suspense>
      </div>
    );
  }
}