import React, { PureComponent } from "react";
import Masonry from 'react-masonry-component';
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../actions/themeActions';
class NewsCard extends PureComponent {
  state = {
    postData: [],
    loadData: 1
  }
  componentDidMount() {
    const { postData } = this.props;
    const self = this;
    setTimeout(() =>
      this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
      , 100)

    window.addEventListener('resize', function (event) {
      self.editorHeight();
    }, true);
  }

  onLoadMasonry = () => {
    // const { adjustWidth } = this.props;
    // if (adjustWidth) {
    //   var elem = document.querySelector('.tb_nc_post_container');
    //   var iso = new Isotope(elem, {
    //     itemSelector: '.tb_nc_post_wrapper',
    //     layoutMode: 'masonry',
    //     masonry: {
    //       columnWidth: 1,
    //       horizontalOrder: false

    //     }
    //   });
    //   imagesLoaded(elem, (instance) => {
    //     if (iso) {
    //       iso.layout();
    //       this.editorHeight()
    //     }
    //   });
    // }
    this.editorHeight()
  }


  editorHeight = () => {
    var span = document.querySelector(".tb_nc_post_container");

    if (span) {
      const getHeight = span.style.height;
      if (getHeight) {
        const height = getHeight.split("px");
        if (height && JSON.stringify(height) && height[0] > 0) {
          this.props.managePostHeight(`${getHeight}`);
        }
      }
    }
  }


  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
  }
  render() {
    const { postData, completeDataObject, adjustWidth, wall, postSize, clickToShowPopUp, onClickToCTA } = this.props;
    const { loadData } = this.state;


    return (
      <Masonry
        className={`tb_nc_post_container`}
        tabIndex='0'
        aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
        elementType={'div'}
        options={{
          transitionDuration: 0,
          percentPosition: true,
          itemSelector: '.tb_nc_post_wrapper',
          horizontalOrder: true,
          resize: true,
        }}
        disableImagesLoaded={false}
        updateOnEachImageLoad={false}
        imagesLoadedOptions={{}}
        enableResizableChildren={true}>{
          loadData ?
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];

              return <Card ownerId={wall.Wall.owner} itemData={cardData} key={`nc_card${cardData.id}`} itemIndex={index} adjustWidth={cardData.highlight == 1 ? adjustWidth * 2 : adjustWidth} postSize={postSize} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} />
            }) : null
        }
      </Masonry >
    );
  }
}

const mapStateToProps = state => {
  return {
    postHeight: state.postHeight.data
  }
}
const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewsCard)