import React, { memo, useMemo } from "react";
import { timeAgo } from '../../../../utils'
const Time = (props) => {
  const { postTime, timeClass, authorNameStyle } = props;
  const momentTime = useMemo(() => {
    return timeAgo(postTime)
  }, [])

  return <div className={timeClass} style={authorNameStyle}>{momentTime}</div>
}

export default memo(Time)